.header{
    height: 100vh;
    width: 100%;
    &__img{
        display: block;
    }
    .logo{
        color: #e70201;
        font-family: "Fugaz One";
        font-size: 28px;
        text-transform: uppercase;
        position: absolute;
        top: 20px;
        left: 20px;
        font-weight: 500;
    }
    .white{
        color: #fff;
        font-family: "Fugaz One";
        font-size: 28px;
        text-transform: uppercase;
    }
    // .logo{
    //     background: url("../../img/Logo.svg");
    //     width: 100%;
    //     visibility: visible;
    //     display: table-cell;
    //     height: 37px;
    // }
    &__video{
        position: absolute;
        left: 5%;
        top: 4%;
        width: 90px;
    }
}



@media(min-width: 576px){
    .header{
        .logo{
            position: absolute;
            top: 30px;
            left: 30px;
        }
        &__video{
            position: absolute;
            left: 20%;
            top: 7%;
            width: 320px;
        }
        &__video iframe{
            width: 320px;
        }
    }
}
@media(min-width: 576px){
    .header{
        .logo{
            position: absolute;
            top: 40px;
            left: 40px;
        }
    }
}
@media(min-width: 768px){
    .header{
        .logo{
            font-size: 48px;
            position: absolute;
            top: 40px;
            left: 40px;
        }
        .white{
            font-size: 48px;
        }
        &__video{
            position: absolute;
            left: 10%;
            top: 7%;
        }
        &__video iframe{
            width: 600px;
            height: 300px;
        }
    }
}
@media(min-width: 976px){
    .header{
        &__video{
            position: absolute;
            left: 10%;
            top: 10%;
        }
        &__video iframe{
            width: 750px;
            height: 300px;
        }
    }
  
}